}

.simcal-default-calendar-grid .simcal-events-toggle {
    background: 0 0;
    display: block;
    font-size: 10px;
    padding: 2px 0
}

.simcal-event-bubble {
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, .1);
    box-shadow: 0 1px 2px rgba(0, 0, 0, .1)
}

.simcal-event-bubble .simcal-event-details .simcal-event-description,
.simcal-event-bubble .simcal-event-details p {
    margin: 0 0 1em
}

//modifications
.simcal-calendar > * {
    border-collapse: collapse;
}

//calendar head
.simcal-calendar-head {
    height: 70px;
}

.simcal-calendar-head,
.simcal-current-month,
.simcal-current-year {
    background: #541510;
    color: white;
}

.simcal-calendar-head tr:first-child {
    height: 70px;
}

.simcal-calendar-head th {
    padding: .5em 0;
}

//remove bar between month and year
.simcal-nav h3 span:last-child {
    margin-left: -7px;
}

.simcal-nav h3 span:first-child {
    padding-right: 6px;
}

//calendar body
.simcal-default-calendar-grid.simcal-default-calendar-light .simcal-day-void {
    color: white;
}

.simcal-default-calendar-grid.simcal-default-calendar-light .simcal-day-number {
    background: #c2ab7f;
    color: white;
    padding: .35em 0;
}

.simcal-month td {
    min-height: 140px;
}

.simcal-event-title {
    font-size: 12px;
}

.simcal-day-has-events .simcal-day-label {
    background: #c2ab7f !important;
}

.simcal-day div {
    min-height: 140px !important;
}


