/* qTip2 v2.2.1 | Plugins: tips viewport | Styles: core | qtip2.com | Licensed MIT | Wed Aug 12 2015 16:16:42 */

.qtip {
    position: absolute;
    left: -28000px;
    top: -28000px;
    display: none;
    max-width: 280px;
    min-width: 50px;
    font-size: 10.5px;
    line-height: 12px;
    direction: ltr;
    box-shadow: none;
    padding: 0
}

.qtip-content {
    position: relative;
    padding: 5px 9px;
    overflow: hidden;
    text-align: left;
    word-wrap: break-word
}

.qtip-titlebar {
    position: relative;
    padding: 5px 35px 5px 10px;
    overflow: hidden;
    border-width: 0 0 1px;
    font-weight: 700
}

.qtip-titlebar+.qtip-content {
    border-top-width: 0!important
}

.qtip-close {
    position: absolute;
    right: -9px;
    top: -9px;
    z-index: 11;
    cursor: pointer;
    outline: 0;
    border: 1px solid transparent
}

.qtip-titlebar .qtip-close {
    right: 4px;
    top: 50%;
    margin-top: -9px
}

* html .qtip-titlebar .qtip-close {
    top: 16px
}

.qtip-icon .ui-icon,
.qtip-titlebar .ui-icon {
    display: block;
    text-indent: -1000em;
    direction: ltr
}

.qtip-icon,
.qtip-icon .ui-icon {
    -moz-border-radius: 3px;
    -webkit-border-radius: 3px;
    border-radius: 3px;
    text-decoration: none
}

.qtip-icon .ui-icon {
    width: 18px;
    height: 14px;
    line-height: 14px;
    text-align: center;
    text-indent: 0;
    font: 400 bold 10px/13px Tahoma, sans-serif;
    color: inherit;
    background: -100em -100em no-repeat
}

.qtip-default {
    border: 1px solid #F1D031;
    background-color: #FFFFA3;
    color: #555
}

.qtip-default .qtip-titlebar {
    background-color: #FFEF93
}

.qtip-default .qtip-icon {
    border-color: #CCC;
    background: #F1F1F1;
    color: #777
}

.qtip-default .qtip-titlebar .qtip-close {
    border-color: #AAA;
    color: #111
}

.qtip .qtip-tip {
    margin: 0 auto;
    overflow: hidden;
    z-index: 10
}

.qtip .qtip-tip,
x:-o-prefocus {
    visibility: hidden
}

.qtip .qtip-tip,
.qtip .qtip-tip .qtip-vml,
.qtip .qtip-tip canvas {
    position: absolute;
    color: #123456;
    background: 0 0;
    border: 0 dashed transparent
}

.qtip .qtip-tip canvas {
    top: 0;
    left: 0
}

.qtip .qtip-tip .qtip-vml {
    behavior: url(#default#VML);
    display: inline-block;
    visibility: visible
}